.catalog {
  width: 320px;
  margin: 0 auto;
  background-color: @lightest-grey;

  @media (min-width: @tablet-width) {
    width: 768px;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
  }
}

.catalog__container {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
  }
}

.catalog__filter {
  padding: 0 20px 48px;
}

.catalog__banner {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: @primary;
  text-transform: uppercase;
  padding: 50px 0 4px;
}

.catalog__intro {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  padding-bottom: 30px;

  @media (min-width: @tablet-width) {
    font-weight: 700;
    font-size: 42px;
    line-height: 64px;
  }
}

.catalog__filter-container {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(3, 240px);
    gap: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr;
    grid-template-rows: 140px, 230px, 240px;
  }
}

.catalog__filter-button {
  background-color: @white;
  color: @primary;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 67px;

  &:hover {
    color: @white;
  }

  &:focus {
    color: @white;
  }

  &:active {
    color: @white;
  }

  &:disabled {
    background-color: @light-grey;
    color: @white;
  }

  @media (min-width: @desktop-width) {
    padding: 8px 47px;
  }
}
