.button {
  border: none;
  color: @white;
  background-color: @primary;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: @font;
  border-radius: 4px;

  &:hover {
    background-color: @primary-lighter;
  }

  &:focus {
    background-color: @primary-lighter;
  }

  &:active {
    background-color: @primary-darker;
  }

  &:disabled {
    background-color: @grey;
  }
}
